@import url(https://fonts.googleapis.com/css?family=Roboto:500);
@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Acme&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Akaya+Telivigala&display=swap");
@import url("https://fonts.googleapis.com/css2?family=RocknRoll+One&display=swap");

:root {
  --Font-1: Acme;
  --Font-2: RocknRoll One;
}

body {
  background-image: url("bg-11.png");
  background-repeat: no-repeat;
  background-size: contain; 
 background-size: cover;
  /* background-color: whitesmoke; */
}

.App {
  text-align: center;
  padding: 2% 5%;
}

.App h1 {
  font-family: var(--Font-1);
  /* background:  */
  font-size: 50px;
  margin-bottom: 20px;
  margin-top: 0px;
  background: linear-gradient(
    90deg,
    rgba(46, 39, 170, 1) 0%,
    rgba(168, 41, 193, 1) 41%,
    rgba(44, 74, 203, 1) 61%,
    rgba(199, 26, 198, 1)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  /* text-shadow: 1px 1px 1px white; */
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}

.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.city {
  width: 250px;
  text-align: center;
  margin-bottom: 10px;
}

.city-search {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
}

.city-search-instruction {
  margin-bottom: 15px;
  margin-top: 0;
  font-family: var(--Font-2);
  font-size: 18px;
}

.suggestions {
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 83px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

li {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
  background: linear-gradient(
    90deg,
    rgba(46, 39, 170, 1) 0%,
    rgba(168, 41, 193, 1) 41%,
    rgba(44, 74, 203, 1) 61%,
    rgba(199, 26, 198, 1)
  );
  /* background: linear-gradient(90deg, rgba(99,92,223,1) 0%, rgba(168,41,193,1) 41%, rgba(94,120,227,1) 61%, rgba(199,26,198,1) 100%); */
  margin-right: 30px;
  box-shadow: 3px 2px 3px black;
}

li:nth-child(even){
  background: rgba(44, 74, 203, 1);
}

.details-btn {
  position: absolute;
  left: 5px;
  bottom: 5px;
  padding: 5px 8px;
  margin-top: 10px !important;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.85);
  /* text-shadow: 2px 2px 2px white; */
  background-color: rgba(0, 0, 0, 0.35);
  box-shadow: 1px 1px 1px black;
  transition: all 0.3s ease-in-out;
}

.details-btn:hover {
  color: rgba(0, 0, 0, 1);
  background-color: rgba(255, 255, 255, 0.95);
}

.event-list-container {
  /* display: flex;
  justify-content: space-between; */
  margin-top: 40px;
}

.event-list {
  /* margin: 0 auto; */
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(450px, 1fr));
  grid-row-gap: 20px;
}

.event-heading {
  display: flex;
  justify-content: space-between;
}

.event-link {
  background-color: rgba(0, 0, 0, 0.35);

  width: 80px;
  height: 40px;
  border-radius: 5px;
  box-shadow: 1px 1px 1px black;
  transition: all 0.3s ease-in-out;
  /* font-family: var(--Font-2); */
}

.event-link a {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.85);
}

.event-link:hover {
  transform: scaleX(1.05) scaleY(1.05);
}

.summary {
  color: white;
  text-shadow: 1px 1px 8px black;
}

.event-overview-info {
  margin-bottom: 20px;
}

.location,
.start-time {
  color: white;
  text-shadow: 1px 1px 8px black;
}

.event-details {
  color: whitesmoke;
  margin: 10px 0px 20px;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.number-of-events-label h3 {
  font-family: var(--Font-2);
  font-size: 17px;
  /* font-weight: bold; */
  margin-bottom: 20px;
}

.number-of-events {
  width: 250px;
  margin: 20px auto;
  text-align: center;
  margin-top: 5px;
}

/* .number-of-events-input {
  display: flex;
} */

.event-number {
  width: 60px;
  margin-top: -15px;
  margin-bottom: 20px;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.Alert {
  /* position: fixed; */
  /* margin-left: 35%;
  margin-top: 45px; */
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  margin-left: 150px;
}

.data-vis-wrapper h4 {
  /* font-family: var(--Font-1); */
  font-size: 18px;
  color: rgba(0, 0, 0, .75);
  width: 200px;
  text-shadow: 0.5px 0.5px 1px white;
  margin-left: -100px;
  /* background: linear-gradient(90deg, rgba(46,39,170,1) 0%, rgba(168,41,193,1) 41%, rgba(44,74,203,1) 61%, rgba(199,26,198,1));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; */
}

.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
  /* margin-left: 100px; */
}

.recharts-surface:first-child {
  overflow: visible;
  /* margin-left: 10px !important; */
}

.recharts-surface:last-child {
  margin-left: 100px !important;
} 

