  margin-left: 100px !important;
} */

tspan {
  /* font-family: var(--Font-2); */
  color: black !important;
  font-weight: bold;
  font-size: 18px;
  text-shadow: 0.3px 0.3px .2px black
}

.recharts-surface tspan {
  color: black !important;
  opacity: 1;
}
